.App {
  box-sizing: border-box;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.head{
  background-color: #09f;
  color: #fff;
  text-align: center;
  padding: .5rem;
}

.main{
  flex: 1;
  overflow: auto;
}
.show{
  display: block;
}
.hide{
  display: none;
}
.menu{
  box-shadow: 0 0 3px rgb(240, 240, 240);
  display: flex;
}
.menu_item{
  flex: 1;
  background-color:rgb(240, 240, 240);
  color: #999;
  padding: .5rem 0;
  transition: 100ms;
  text-align: center;
}
.menu_item:hover{
  transition: 100ms;
  background-color: rgb(97, 192, 255);
}
.menu_item_active{
  background-color: rgb(97, 192, 255);
  color: #fff;
}
.weapon_container{
  padding: .5rem;
}
.newWeapon{
  box-shadow: 0 0 8px rgb(97, 192, 255);
  text-align: center;
  font-size: 2rem;
  color: rgb(97, 192, 255);
}
.newWeapon:hover{
  color: #fff;
  background-color:  rgb(97, 192, 255);
}

.mderContainer{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.3);
}
.InputsBox{
  border-radius: 4px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background-color: #fff;
  padding: .2rem;
  width: 80%;
  min-height: 30%;
  overflow: auto;
}

.button{
  border: none;
  outline: none;
  padding: .2rem .4rem;
  border-radius: 4px;
}

.button_red{
  color: #fff;
  background-color: rgb(245, 73, 73);
}
.button_red:hover{
  color: #fff;
  background-color: rgb(255, 144, 144);
}

.righttop{
  position: absolute;
  top: .2rem;
  right: .2rem;
}